export function createHTMLComponent(
  node: Element,
  children: React.ReactElement[]
): React.ReactElement {
  // if (node.nodeName === "img") {
  //   return createImageComponent(node);
  // }

  console.log("...getAttributes(node)", getAttributes(node));

  return React.createElement(
    node.nodeName,
    {
      ...getAttributes(node),
    },
    ...children
  );

  // return React.cloneElement(children[0], {
  //   ...getAttributes(node),
  //   children: children.slice(1),
  // });
}
